 .ds {
   display: none;
 }
 @media (max-width: 800px) {
   .schedule {
     display: inline-grid;
     overflow: hidden;
     grid-template-columns: [time] 40px [sunday] minmax(auto, 125px) [monday] minmax(
         auto,
         125px
       ) [tuesday] minmax(auto, 125px) [wednesday] minmax(auto, 125px) [thursday] minmax(
         auto,
         125px
       ) [friday] minmax(auto, 125px) [saturday] minmax(auto, 125px);
     grid-template-rows: [header] 40px [s] 40px 59px [sf] 40px 59px [st] 40px 59px [sff] 40px 59px [e] 40px 59px [ef] 40px 59px [et] 40px 59px [eff] 40px 59px [n] 40px 59px [nf] 40px 59px [nt] 40px 59px [nff] 40px 59px;
   }
   .schedule_time {
     -ms-transform: rotate(-90deg) translate(-100px);
     -webkit-transform: rotate(-90deg) translate(-100px);
     transform: rotate(-90deg) translate(-100px);
     -webkit-transform-origin: left 50%;
     -ms-transform-origin: left 50%;
     transform-origin: left top;
     width: 90px;
   }
   .dl {
     display: none;
   }
   .ds {
     display: block;
   }
   .grid-last {
     height: 98px;
   }
 }
 .card {
   border-color: var(--border-color);
   background-color: var(--card-color);
 }
 .card.card-callout {
   border-left-width: 0.4rem;
   border-left-color: var(--primary-color);
 }
 .card .card-header {
   background-color: transparent;
   border-bottom: none;
 }
 .card .card-footer {
   border-color: var(--border-color);
 }
 .room_book .room {
   position: relative;
   min-width: 100px;
 }
 .room_book .room input[type="checkbox"] {
   position: absolute;
   opacity: 0;
 }
 .room_book .room input[type="checkbox"]:checked + label i {
   color: #8dbfb3;
   padding-top: 5px;
   display: block;
 }
 .room_book .room input[type="checkbox"]:disabled + label {
   cursor: not-allowed;
 }
 .room_book .room input[type="checkbox"]:disabled + label i {
   color: var(--color-400);
   padding-top: 5px;
   display: block;
 }
 .room_book .room input[type="checkbox"]:disabled + label span {
   display: block;
   color: var(--text-color);
   padding-top: 5px;
 }
 .room_book .room label {
   display: block;
   position: relative;
   text-align: center;
   font-size: 14px;
   padding: 4px 0;
   border-radius: 5px;
   cursor: pointer;
 }
 .room_book .room label i {
   color: #ff9691;
   padding-top: 5px;
   display: block;
 }
 .room_book .room label span {
   display: block;
   color: var(--text-color);
   padding-top: 5px;
 }
 .wrapper {
   position: relative;
 }
 .wrapper .timepicker-cf {
   height: 60px;
   width: 100%;
   position: relative;
 }
 .wrapper .timepicker {
   position: absolute;
   top: 200px;
   width: 97%;
   height: 0px;
   background: var(--primary-color);
   overflow: hidden;
 }
 .wrapper .timepicker .owl,
 .wrapper .timepicker .owl-stage {
   width: 100%;
   height: 60px;
   display: flex;
   align-items: center;
 }
 .wrapper .timepicker .owl .owl-item,
 .wrapper .timepicker .owl-stage .owl-item {
   font-size: 1.1em;
   display: inline-block;
   font-weight: 600;
   color: white;
   text-align: center;
   min-width: 100px;
   opacity: 0.4;
   cursor: pointer;
   transition: all 0.2s ease-in-out;
 }
 .wrapper .timepicker .owl .center-n,
 .wrapper .timepicker .owl-stage .center-n {
   font-size: 1.5em;
   opacity: 0.7;
 }
 .wrapper .timepicker .owl .center,
 .wrapper .timepicker .owl-stage .center {
   font-size: 2em;
   opacity: 1;
 }
 .wrapper .fade-l {
   position: absolute;
   height: 100%;
   width: 150px;
   left: 0;
   top: 0;
   background: -moz-linear-gradient(left, #fff 0%, rgba(255, 255, 255, 0) 100%);
   background: -webkit-linear-gradient(
     left,
     #fff 0%,
     rgba(255, 255, 255, 0) 100%
   );
   background: linear-gradient(to right, #fff 0%, rgba(255, 255, 255, 0) 100%);
   filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=1 );
 }
 .wrapper .fade-r {
   position: absolute;
   height: 100%;
   width: 150px;
   right: 0;
   top: 0;
   background: -moz-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #fff 100%);
   background: -webkit-linear-gradient(
     left,
     rgba(255, 255, 255, 0) 0%,
     #fff 100%
   );
   background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #fff 100%);
   filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=1 );
 }
 .wrapper .owl-prev,
 .wrapper .owl-next {
   z-index: 20;
   position: absolute;
   width: 20px;
   top: 20px;
   color: #667483;
   cursor: pointer;
   transition: 0.2s ease-in-out;
   background: none;
   border: 0;
   font-size: 1em;
 }
 .wrapper .owl-prev {
   left: 20px;
 }
 .wrapper .owl-prev:before {
   content: "\f053";
   font-family: FontAwesome;
 }
 .wrapper .owl-prev:hover {
   left: 18px;
   transition: 0.2s ease-in-out;
 }
 .wrapper .owl-next {
   right: 20px;
 }
 .wrapper .owl-next:hover {
   right: 18px;
   transition: 0.2s ease-in-out;
 }
 .wrapper .owl-next:before {
   content: "\f054";
   font-family: FontAwesome;
 }
 .wrapper .inner-wrap .request {
   display: flex;
   align-items: center;
   justify-content: space-between;
   width: 100%;
   background: var(--primary-color);
   color: #fff;
   font-weight: 600;
   cursor: pointer;
   opacity: 1;
 }
 .wrapper .inner-wrap .request br {
   display: none;
 }
 .wrapper .inner-wrap .request .sendRequest {
   position: absolute;
   right: 20px;
   top: 16px;
 }
 .wrapper .inner-wrap .request .sendRequest:before {
   content: "\f054";
   font-family: FontAwesome;
 }
 .wrapper .inner-wrap .request.disabled {
   opacity: 0.8;
   pointer-events: none;
   transition: all 0.3s ease-in-out;
 }
 .wrapper .inner-wrap .disabled span {
   display: none;
 }
 .wrapper #calendar {
   overflow: hidden;
   position: relative;
 }
 .wrapper #calendar .monthChange {
   background: #fff;
   width: 100%;
   height: 0;
   position: absolute;
   top: 160px;
   z-index: 20;
 }
 .wrapper #calendar .ui-datepicker {
   display: none;
   width: 100%;
   margin: auto;
   padding: 30px 0px;
   text-transform: uppercase;
   background: var(--card-color);
   transition: all 0.2s ease-in-out;
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-header {
   position: relative;
   margin-bottom: 10px;
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-title {
   text-align: center;
   font-weight: 700;
   color: #353c42;
 }
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-header
   .ui-datepicker-title
   .ui-datepicker-month {
   position: relative;
   padding-right: 5px;
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-prev,
 .wrapper #calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-next {
   top: -5px;
   position: absolute;
   padding: 5px;
   cursor: pointer;
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-prev {
   left: 0;
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-next {
   right: 0;
 }
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-header
   .ui-datepicker-prev:before {
   content: "\f053";
   font-family: FontAwesome;
 }
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-header
   .ui-datepicker-next:after {
   content: "\f054";
   font-family: FontAwesome;
 }
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-header
   .ui-datepicker-prev
   span,
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-header
   .ui-datepicker-next
   span {
   display: inline-block;
   text-indent: -9999px;
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-calendar {
   width: 100%;
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-calendar th {
   line-height: 30px;
   padding-top: 15px;
   text-align: center;
   font-weight: 600;
   font-size: 0.95em;
   color: var(--font-color);
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-calendar td {
   padding: 0 7px;
   text-align: center;
   height: 34px;
   font-weight: 600;
   cursor: pointer;
   box-sizing: border-box;
   transition: all 0.2s ease-in-out;
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-calendar td:hover {
   background: var(--secondary-color);
   transition: all 0.2s ease-in-out;
 }
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-calendar
   .ui-datepicker-current-day {
   background: var(--secondary-color);
   border-bottom: 2px solid #353c42;
 }
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-calendar
   .ui-datepicker-current-day.open {
   margin-bottom: 100px;
 }
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-calendar
   .ui-datepicker-today
   .ui-state-default {
   border-bottom: 2px solid rgba(53, 60, 66, 0.5);
 }
 .wrapper #calendar .ui-datepicker .ui-datepicker-calendar .ui-state-default {
   text-decoration: none;
   color: var(--font-color);
 }
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-calendar
   .ui-datepicker-other-month {
   pointer-events: none;
 }
 .wrapper
   #calendar
   .ui-datepicker
   .ui-datepicker-calendar
   .ui-datepicker-other-month
   .ui-state-default {
   color: #919ba3;
 }
